/* MainPage.css - СТРОГИЙ И СЕКСУАРНЫЙ ДИЗАЙН */
.main-page {
  padding: 24px;
  max-width: 1600px;
  margin: 0 auto;
  background: var(--color-background);
  /* min-height: 100vh; */
}

/* ========== СТАТИСТИКА - СТЕЛС РЕЖИМ ========== */
.stats-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(220px, 1fr));
  gap: 18px;
  margin-top: 20px;
  margin-bottom: 20px;
}

/* Общий стиль карточек */
.stat-card {
  display: flex;
  align-items: center;
  padding: 20px;
  border-radius: 18px;
  background: linear-gradient(135deg, #ffffff, #f7f7f7);
  box-shadow: 0 4px 14px rgba(0, 0, 0, 0.08);
  transition: transform 0.2s ease, box-shadow 0.2s ease;
}

.stat-card:hover {
  transform: translateY(-4px);
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.12);
}

.stat-card .stat-content {
  display: flex;
  flex-direction: column;
}

/* Заголовки и числа */
.stat-value {
  font-size: 22px;
  font-weight: 700;
  color: #222;
  margin-bottom: 4px;
}

.stat-label {
  font-size: 15px;
  font-weight: 500;
  color: #444;
}

.stat-subtext {
  font-size: 13px;
  opacity: 0.7;
  margin-top: -2px;
}

/* Акцентные яркие цвета для каждой карточки */
.stat-card.income {
  background: linear-gradient(135deg, #ffecc7, #ffd39a);
}

.stat-card.workouts {
  background: linear-gradient(135deg, #d4f6ff, #aee9ff);
}

.stat-card.average {
  background: linear-gradient(135deg, #f8e1ff, #e9c8ff);
}

.stat-card.clients {
  background: linear-gradient(135deg, #ffe1e1, #ffcccc);
}

.stat-card.active {
  background: linear-gradient(135deg, #d3ffe2, #b3f7cd);
}

.stat-card.new {
  background: linear-gradient(135deg, #fff3c6, #ffe89b);
}

/* Для иконок в третьей карточке (где Avatar нет) */
.stat-card svg {
  margin-right: 14px;
  font-size: 40px;
  opacity: 0.75;
}

/* ========== СЕКЦИИ ТРЕНИРОВОК - УЛЬТРА СОВРЕМЕННЫЕ ========== */
.workouts-section {
  margin-bottom: 48px;
  position: relative;
}

.section-header {
  font-weight: 800;
  margin-bottom: 28px;
  display: flex;
  align-items: center;
  gap: 12px;
  color: var(--color-text-primary);
  font-size: 1.4rem;
  letter-spacing: -0.5px;
  padding-bottom: 12px;
  border-bottom: 2px solid var(--color-border);
}

.section-header .icon {
  font-size: 1.5rem;
  background: var(--gradient-primary);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

/* ========== КОНТЕЙНЕР КАРТОЧЕК - ПРОФЕССИОНАЛЬНЫЙ СКРОЛЛ ========== */
.workouts-container {
  position: relative;
  display: flex;
  align-items: center;
  padding: 0 8px;
}

.scroll-button {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  z-index: 20;
  background: var(--color-card-background) !important;
  border: 1px solid var(--color-border) !important;
  box-shadow: var(--shadow-lg) !important;
  width: 44px;
  height: 44px;
  backdrop-filter: blur(10px);
  transition: all var(--transition-normal) !important;
}

.scroll-button:hover {
  background: var(--color-primary) !important;
  color: white !important;
  transform: translateY(-50%) scale(1.1);
  box-shadow: var(--shadow-xl) !important;
}

.scroll-button.left {
  left: -12px;
}

.scroll-button.right {
  right: -12px;
}

.cards-wrapper {
  display: flex;
  overflow-x: auto;
  gap: 24px;
  padding: 20px 8px;
  scrollbar-width: thin;
  scrollbar-color: var(--color-primary) transparent;
  scroll-behavior: smooth;
}

.cards-wrapper::-webkit-scrollbar {
  height: 6px;
}

.cards-wrapper::-webkit-scrollbar-track {
  background: transparent;
}

.cards-wrapper::-webkit-scrollbar-thumb {
  background: var(--color-primary);
  border-radius: 3px;
}

.cards-wrapper::-webkit-scrollbar-thumb:hover {
  background: var(--color-primary-hover);
}

/* ========== КАРТОЧКИ КЛИЕНТОВ - ШЕДЕВР ДИЗАЙНА ========== */
.client-card {
  min-width: 320px;
  flex-shrink: 0;
  display: flex;
  flex-direction: column;
  border-radius: var(--radius-2xl);
  border: 1px solid var(--color-border);
  box-shadow: var(--shadow-sm);
  overflow: hidden;
  cursor: pointer;
  position: relative;
}

.client-card::before {
  content: ''  ;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: var(--gradient-primary);
  opacity: 0;
  transition: opacity var(--transition-normal);
  z-index: 1;
}

.client-card:hover {
  transform: translateY(-6px) scale(1.02);
  box-shadow: var(--shadow-xl);
  border-color: var(--color-primary);
}

.client-card:hover::before {
  opacity: 0.03;
}

.client-header {
  padding: 24px;
  display: flex;
  align-items: center;
  color: black;
  position: relative;
  z-index: 2;
  border-bottom: 1px solid #E2E8F0 !important;
  background: #7c3aed36;
}


.client-avatar {
  width: 56px !important;
  height: 56px !important;
  margin-right: 2vh;
  background-color:  var(--color-primary) !important ;
  color:  white ;
  font-weight: 700;
  font-size:  1.5rem ;
}

.client-info {
  flex: 1;
}

.client-name {
  font-weight: 800;
  margin-bottom: 4px;
  font-size: 1.1rem;
  letter-spacing: -0.3px;
}

.client-age {
  font-size: 0.9rem;
  font-weight: 500;
}

.client-content {
  padding: 24px;
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  position: relative;
  z-index: 2;
}

/* Чипы - минималистичные и стильные */
.chips-container {
  display: flex;
  justify-content: space-between;
  margin-bottom: 20px;
  flex-wrap: wrap;
  gap: 8px;
}

.client-chip {
  font-weight: 700 !important;
  border: 1px solid var(--color-border) !important;
  background: var(--color-surface) !important;
  color: var(--color-text-primary) !important;
  height: 32px !important;
  transition: all var(--transition-fast) !important;
}

.client-chip:hover {
  border-color: var(--color-primary) !important;
  transform: translateY(-1px);
}

/* Детали тренировки */
.workout-details {
  margin-bottom: 20px;
}

.detail-item {
  display: flex;
  align-items: center;
  margin-bottom: 10px;
  font-weight: 600;
  color: var(--color-text-primary);
  font-size: 0.9rem;
}

.detail-item .icon {
  margin-right: 10px;
  font-size: 18px;
  color: var(--color-primary);
  opacity: 0.8;
}

.divider {
  margin: 16px 0;
  border-color: var(--color-border);
  opacity: 0.6;
}

.phone-info {
  display: flex;
  align-items: center;
  font-weight: 600;
  color: var(--color-text-primary);
  font-size: 0.9rem;
}

.phone-info .icon {
  margin-right: 10px;
  font-size: 18px;
  color: var(--color-primary);
}

/* ========== КНОПКА ОТМЕТКИ - УЛЬТРА КЛИКАБЕЛЬНАЯ ========== */
.mark-button {
  border-radius: var(--radius-lg) !important;
  font-weight: 800 !important;
  text-transform: none !important;
  background: var(--color-primary) !important;
  color: #fff !important;
  margin-top: auto !important;
  padding: 12px !important;
  font-size: 0.9rem !important;
}

.mark-button:hover {
  background: #836f86 0% !important;
  transform: translateY(-2px);
  border-color: var(--color-primary);
}

/* ========== КАРТОЧКА ДОБАВЛЕНИЯ - СТИЛЬНЫЙ ПЛЮС ========== */
.add-card {
  min-width: 320px;
  flex-shrink: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  border-radius: var(--radius-2xl);
  border: 2px dashed var(--color-border);
  background: transparent;
  transition: all var(--transition-normal);
  cursor: pointer;
  position: relative;
  overflow: hidden;
}

.add-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: var(--gradient-primary);
  opacity: 0;
  transition: opacity var(--transition-normal);
}

.add-card:hover {
  background: var(--color-card-hover);
  border-color: var(--color-primary);
  transform: translateY(-4px) scale(1.02);
  box-shadow: var(--shadow-lg);
}

.add-card:hover::before {
  opacity: 0.05;
}

.add-card-content {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 60px 40px;
  position: relative;
  z-index: 2;
}

.add-icon {
  font-size: 52px;
  color: var(--color-primary);
  margin-bottom: 16px;
  transition: transform var(--transition-normal);
}

.add-card:hover .add-icon {
  transform: scale(1.1) rotate(90deg);
}

.add-text {
  color: var(--color-text-primary);
  font-weight: 800;
  text-align: center;
  font-size: 1rem;
}

/* ========== МОДАЛЬНЫЕ ОКНА - ЛЮКС КЛАСС ========== */
.modal-container {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 440px;
  max-width: 90vw;
  background: var(--color-card-background);
  border-radius: var(--radius-2xl);
  box-shadow: var(--shadow-xl);
  padding: 32px;
  border: 1px solid var(--color-border);
  backdrop-filter: blur(20px);
}

.modal-title {
  font-weight: 800;
  margin-bottom: 28px;
  text-align: center;
  color: var(--color-text-primary);
  font-size: 1.3rem;
  letter-spacing: -0.5px;
}

.form-field {
  margin-bottom: 24px;
}

.field-label {
  font-weight: 700;
  margin-bottom: 10px;
  color: var(--color-text-primary);
  display: block;
  font-size: 0.9rem;
}

.search-input .MuiOutlinedInput-root {
  border-radius: var(--radius-lg) !important;
  background: var(--color-surface);
}

.search-input .search-icon {
  margin-right: 8px;
  color: var(--color-text-secondary);
}

.modal-actions {
  display: flex;
  justify-content: center;
  gap: 16px;
  margin-top: 16px;
}

.action-button {
  border-radius: var(--radius-lg) !important;
  font-weight: 700 !important;
  text-transform: none !important;
  padding: 12px 28px !important;
  font-size: 0.9rem !important;
  transition: all var(--transition-normal) !important;
}

.cancel-button {
  border-color: var(--color-border) !important;
  color: var(--color-text-secondary) !important;
}

.cancel-button:hover {
  border-color: var(--color-border-hover) !important;
  background: var(--color-surface) !important;
  transform: translateY(-1px);
}

.submit-button {
  background: var(--color-primary) !important;
  color: white !important;
  box-shadow: var(--shadow-sm) !important;
}

.submit-button:hover {
  background: var(--color-primary-hover) !important;
  transform: translateY(-2px);
  box-shadow: var(--shadow-lg) !important;
}

.submit-button:disabled {
  background: var(--color-disabled-background) !important;
  color: var(--color-disabled-text) !important;
  transform: none !important;
  box-shadow: none !important;
}

/* ========== АДАПТИВНОСТЬ - ИДЕАЛЬНАЯ НА ЛЮБОМ УСТРОЙСТВЕ ========== */
@media (max-width: 1200px) {
  .stats-grid {
    grid-template-columns: repeat(3, 1fr);
  }
}

@media (max-width: 768px) {
  .main-page {
    padding: 16px;
  }

  .stats-grid {
    grid-template-columns: repeat(2, 1fr);
    gap: 16px;
  }

  .stat-card {
    padding: 20px;
  }

  .stat-value {
    font-size: 1.5rem;
  }

  .section-header {
    font-size: 1.2rem;
    margin-bottom: 24px;
  }

  .client-card,
  .add-card {
    min-width: 280px;
  }

  .scroll-button {
    width: 40px;
    height: 40px;
  }

  .modal-container {
    width: 95vw;
    padding: 24px;
  }
}

@media (max-width: 480px) {
  .stats-grid {
    grid-template-columns: 1fr;
  }

  .client-card,
  .add-card {
    min-width: 260px;
  }

  .client-header {
    padding: 20px;
  }

  .client-content {
    padding: 20px;
  }

  .client-avatar {
    width: 52px;
    height: 52px;
    font-size: 1.3rem;
  }

  .chips-container {
    flex-direction: column;
    align-items: flex-start;
  }
}

/* ========== АНИМАЦИИ - ПЛАВНОСТЬ И СТИЛЬ ========== */
@keyframes cardAppear {
  from {
    opacity: 0;
    transform: translateY(20px);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.client-card {
  animation: cardAppear 0.4s ease-out;
}

/* Улучшенные скролл-бары для вебкита */
.cards-wrapper::-webkit-scrollbar-thumb {
  background: linear-gradient(135deg, var(--color-primary), var(--color-secondary));
}

.cards-wrapper::-webkit-scrollbar-track {
  background: var(--color-surface);
  border-radius: 3px;
}


/* Микро-интерактивности */
.client-chip .MuiChip-icon {
  transition: transform var(--transition-fast);
}

.client-chip:hover .MuiChip-icon {
  transform: scale(1.1);
}

/* Градиентные обводки при фокусе для доступности */
.client-card:focus-visible {
  outline: none;
  box-shadow: 0 0 0 3px var(--color-primary), var(--shadow-lg);
}

.mark-button:focus-visible {
  outline: 2px solid white;
  outline-offset: 2px;
}