/* Обновленные CSS переменные с современной палитрой */
:root {
  /* Основные цвета */
  --color-primary: #7c3aed;
  --color-primary-hover: #6D28D9;
  --color-secondary: #8B5CF6;
  --color-accent: #10B981;
  
  /* Фоны */
  --color-background: #FFFFFF;
  --color-surface: #ffffff;
  --color-card-background: #FFFFFF;
  --color-card-hover: #F1F5F9;
  
  /* Текст */
  --color-text-primary: #1E293B;
  --color-text-secondary: #64748B;
  --color-text-muted: #94A3B8;
  
  /* Границы */
  --color-border: #E2E8F0;
  --color-border-hover: #CBD5E1;
  
  /* Кнопки */
  --color-button-primary: #7C3AED;
  --color-button-primary-hover: #6D28D9;
  --color-button-text: #FFFFFF;
  --color-button-secondary: #F1F5F9;
  --color-button-secondary-hover: #E2E8F0;
  
  /* Состояния */
  --color-success: #10B981;
  --color-warning: #F59E0B;
  --color-error: #EF4444;
  --color-info: #3B82F6;
  
  /* Градиенты */
  --gradient-primary: linear-gradient(135deg, #7C3AED 0%, #10B981 100%);
  --gradient-secondary: linear-gradient(135deg, #8B5CF6 0%, #EC4899 100%);
  --gradient-hero: linear-gradient(135deg, #667EEA 0%, #764BA2 100%);
  
  /* Тени */
  --shadow-sm: 0 1px 2px 0 rgba(0, 0, 0, 0.05);
  --shadow-md: 0 4px 6px -1px rgba(0, 0, 0, 0.1), 0 2px 4px -1px rgba(0, 0, 0, 0.06);
  --shadow-lg: 0 10px 15px -3px rgba(0, 0, 0, 0.1), 0 4px 6px -2px rgba(0, 0, 0, 0.05);
  --shadow-xl: 0 20px 25px -5px rgba(0, 0, 0, 0.1), 0 10px 10px -5px rgba(0, 0, 0, 0.04);
  
  /* Радиусы */
  --radius-sm: 4px;
  --radius-md: 8px;
  --radius-lg: 12px;
  --radius-xl: 16px;
  --radius-2xl: 24px;
  
  /* Анимации */
  --transition-fast: 0.15s ease;
  --transition-normal: 0.3s ease;
  --transition-slow: 0.5s ease;
}

[data-theme="dark"] {
  --color-background: #0F172A;
  --color-surface: #1E293B;
  --color-card-background: #1E293B;
  --color-card-hover: #334155;
  --color-text-primary: #F1F5F9;
  --color-text-secondary: #CBD5E1;
  --color-text-muted: #64748B;
  --color-border: #334155;
  --color-border-hover: #475569;
  --color-button-secondary: #334155;
  --color-button-secondary-hover: #475569;
}

/* Базовые стили для Material-UI компонентов */
.MuiInputLabel-root {
  color: var(--color-text-secondary) !important;
  transition: var(--transition-fast) !important;
}

.MuiInputLabel-root.Mui-focused {
  color: var(--color-primary) !important;
}

.MuiOutlinedInput-root {
  border-radius: var(--radius-md) !important;
  transition: var(--transition-fast) !important;
}

.MuiOutlinedInput-root .MuiOutlinedInput-notchedOutline {
  border-color: var(--color-border) !important;
}

.MuiOutlinedInput-root:hover .MuiOutlinedInput-notchedOutline {
  border-color: var(--color-border-hover) !important;
}

.MuiOutlinedInput-root.Mui-focused .MuiOutlinedInput-notchedOutline {
  border-color: var(--color-primary) !important;
  border-width: 2px !important;
}

.MuiInputBase-input {
  color: var(--color-text-primary) !important;
  padding: 12px 14px !important;
}

/* Общие стили для кнопок */
.common-button {
  border-radius: var(--radius-lg) !important;
  padding: 12px 24px !important;
  font-weight: 600 !important;
  text-transform: none !important;
  transition: all var(--transition-normal) !important;
  box-shadow: var(--shadow-sm) !important;
}

.common-button:hover {
  transform: translateY(-2px);
  box-shadow: var(--shadow-lg) !important;
}

.common-primary-button {
  background: var(--color-button-primary) !important;
  color: var(--color-button-text) !important;
}

.common-primary-button:hover {
  background: var(--color-button-primary-hover) !important;
}

/* Анимации */
@keyframes float {
  0%, 100% { transform: translateY(0px); }
  50% { transform: translateY(-10px); }
}

@keyframes pulse {
  0%, 100% { opacity: 1; }
  50% { opacity: 0.7; }
}

.floating {
  animation: float 3s ease-in-out infinite;
}

.pulse {
  animation: pulse 2s ease-in-out infinite;
}